@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto";
  font-display: swap;
  src: url("./fonts/roboto-regular.woff2") format("woff2"), url("./fonts/roboto-regular.woff") format("woff");
}

@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Roboto";
  font-display: swap;
  src: url("./fonts/roboto-bold.woff2") format("woff2"), url("./fonts/roboto-bold.woff") format("woff");
}

html {
  font-family: "Roboto", sans-serif;
  font-size: 16px;
  color: #333;
  background-color: #FFF;
  box-sizing: border-box;
}

.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
}

.site-container {
  max-width: 1470px;
  width: 100%;
  margin: 0 auto;
  padding: 0 15px;
  box-sizing: border-box;
}

.page-wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.page-main {
  flex-grow: 1;
}

.button-reset {
  padding: 0;
  background-color: transparent;
  border: none;
  border-radius: 0;
}

.button_burger {
  width: 70px;
  height: 70px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: background-color 0.3s ease, opacity 0.3s ease;
  opacity: 0.8;
}

.button_burger .icon-burger {
  fill: #333;
  width: 70px;
  height: 70px;
}

.button_burger:active {
  background-color: #ACB5BD;
}

.button_burger:hover {
  opacity: 1;
}

@media (max-width: 740px) {
  .button_burger {
    width: 60px;
    height: 60px;
  }
  .button_burger .icon-burger {
    width: 60px;
    height: 60px;
  }
}

@media (max-width: 320px) {
  .button_burger {
    width: 50px;
    height: 50px;
  }
  .button_burger .icon-burger {
    width: 50px;
    height: 50px;
  }
}

.button {
  position: relative;
  z-index: 10;
  display: inline-block;
  padding: 15px 63px;
  color: #FFF;
  background-color: #724df0;
  border-radius: 10px;
  cursor: pointer;
  transition: background-color .3s ease, opacity .3s ease;
}

.button::after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
  box-shadow: 0 5px 10px rgba(98, 65, 209, 0.5);
  border-radius: 10px;
  opacity: 0;
  transition: opacity .3s ease;
}

.button:hover {
  background-color: #6241d1;
}

.button:hover::after {
  opacity: 1;
}

.button:active {
  background-color: #422b8d;
}

.button-disabled {
  opacity: .5;
  pointer-events: none;
}

.nav__list {
  display: flex;
  flex-direction: column;
}

.nav__item {
  border-bottom: 1px solid #DDE2E5;
  transition: background-color 0.3s ease;
}

.nav__item:nth-child(1) {
  border-top: 1px solid #DDE2E5;
}

.nav__item:hover {
  background-color: #DDE2E5;
}

.nav__item:active {
  background-color: #ACB5BD;
}

.nav__item.nav__item-current-page {
  background-color: #DDE2E5;
}

.nav__item.nav__item-current-page .nav__item_link {
  transform: translateX(10px);
}

.nav__item_link {
  display: block;
  padding: 15px 0;
  padding-left: 15px;
  text-decoration: none;
  color: inherit;
  font-size: 1.5rem;
  font-weight: 700;
  transition: transform 0.3s ease;
}

.nav__item_link span {
  display: inline-block;
}

.nav__item_link:hover {
  transform: translateX(10px);
}

.checkbox {
  position: relative;
  display: inline-block;
  cursor: pointer;
}

.checkbox__input:checked ~ .checkbox__name::after {
  opacity: 1;
}

.checkbox__name {
  position: relative;
  padding-left: 30px;
  font-size: 1.125rem;
}

.checkbox__name::before {
  content: url(../../img/checkbox-default.svg);
  position: absolute;
  top: 50%;
  left: 0;
  width: 25px;
  height: 25px;
  transform: translateY(-50%);
}

.checkbox__name::after {
  content: url(../../img/checkbox-active.svg);
  position: absolute;
  top: 50%;
  left: 0;
  width: 25px;
  height: 25px;
  transform: translateY(-50%);
  opacity: 0;
}

@media (max-width: 360px) {
  .checkbox__name {
    font-size: 0.875rem;
  }
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  width: 100%;
  background-color: #FFF;
  border-bottom: 2px solid #DDE2E5;
  box-shadow: 0 0.5px 7px rgba(221, 226, 229, 0.8);
}

.header__inner {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 50px 0;
}

.header__burger {
  position: absolute;
  top: 50%;
  left: 50px;
  transform: translateY(-50%);
}

@media (max-width: 740px) {
  .header__burger {
    left: 15px;
  }
}

@media (max-width: 480px) {
  .header__inner {
    padding: 30px 0;
  }
}

.main {
  overflow: hidden;
  background-color: #F8F9FA;
}

.menu {
  position: fixed;
  top: 0;
  left: -100%;
  z-index: 120;
  width: 300px;
  height: 100vh;
  background-color: #FFF;
  border-right: 2px solid #DDE2E5;
  box-shadow: 2px 2px 7px rgba(221, 226, 229, 0.8);
}

.menu__head {
  position: relative;
  min-height: 145px;
  background-color: #F8F9FA;
}

.menu__button {
  position: absolute;
  top: 50%;
  left: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 70px;
  height: 70px;
  transform: translateY(-50%);
  transition: background-color 0.3s ease, opacity 0.3s ease, transform 0.3s ease;
  opacity: 0.8;
  cursor: pointer;
  z-index: 10;
}

.menu__button .icon-cross {
  width: 70px;
  height: 70px;
}

.menu__button:active {
  background-color: #ACB5BD;
}

.menu__button:hover {
  opacity: 1;
  transform: translateY(-50%) rotate(180deg);
}

.menu__content {
  height: 100%;
  background-color: #F8F9FA;
}

@media (max-width: 740px) {
  .menu__button {
    left: 15px;
    width: 60px;
    height: 60px;
  }
}

@media (max-width: 480px) {
  .menu__head {
    min-height: 105px;
  }
}

@media (max-width: 375px) {
  .menu {
    width: 100%;
  }
}

@media (max-width: 320px) {
  .menu__button {
    width: 50px;
    height: 50px;
  }
}

.card {
  display: inline-block;
  max-width: 300px;
  width: 100%;
  background-color: #FFF;
  border-radius: 10px;
  text-decoration: none;
  box-shadow: 0 0 10px rgba(172, 181, 189, 0.4);
}

.card__head {
  padding: 20px;
}

.card__title {
  font-size: 1.5rem;
  color: #333;
  text-align: center;
}

.card__body {
  position: relative;
  width: 100%;
  height: 200px;
  overflow: hidden;
}

.card__img {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  transition: transform 0.3s ease;
}

.card__img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.card__bottom {
  padding: 20px;
}

.card__text {
  color: #333;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
  margin-bottom: 20px;
}

.card__button {
  width: 100%;
  text-align: center;
}

.card__button span {
  display: block;
  position: relative;
  height: 17px;
  line-height: 17px;
  overflow: hidden;
  color: transparent;
}

.card__button span::before, .card__button span::after {
  content: attr(data-text);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  margin-right: -50%;
  color: #FFF;
  transition: transform 0.3s ease, opacity 0.3s ease;
}

.card__button span::after {
  transform: translate(-50%, -25px);
  opacity: 0;
}

.card:hover .card__img {
  transform: scale(1.3);
}

.card:hover .card__button::after {
  opacity: 1;
}

.card:hover .card__button span::after {
  transform: translate(-50%, -50%);
  opacity: 1;
}

.card:hover .card__button span::before {
  transform: translate(-50%, 25px);
  opacity: 0;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 200;
  display: none;
}

.modal.js--modal-opened {
  display: block;
}

.modal__overlay {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.2);
}

.modal__content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #FFF;
  max-width: 450px;
  width: 100%;
  border: 1px solid #ACB5BD;
  border-radius: 10px;
  box-shadow: 0 0 10px #ACB5BD;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.modal__content.js--show {
  opacity: 1;
}

.modal__title {
  font-size: 1.5rem;
}

.modal__head {
  padding: 20px;
  padding-bottom: 15px;
  text-align: center;
  border-bottom: 1px solid #ACB5BD;
}

.modal__info {
  padding: 20px;
}

.modal__form {
  width: 100%;
}

@media (max-width: 480px) {
  .modal__content {
    max-width: 100%;
    height: 100%;
    border-radius: 0;
    border: none;
  }
  .modal__inner {
    display: flex;
    flex-direction: column;
    height: 100%;
  }
  .modal__info {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    flex-grow: 1;
  }
}

@media (max-width: 360px) {
  .modal__title {
    font-size: 1rem;
  }
}

.form__label {
  position: relative;
  display: block;
  width: 100%;
  margin-bottom: 20px;
}

.form__input {
  width: 100%;
  border: 1px solid #ACB5BD;
  border-radius: 10px;
  height: 37px;
  padding: 10px;
}

.form__input-file {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  opacity: 0;
}

.form__label_heading {
  font-size: 1.125rem;
  margin-bottom: 10px;
}

.form__custom_input_file {
  position: relative;
  z-index: 10;
  width: 100%;
  border: 1px solid #ACB5BD;
  border-radius: 10px;
  height: 37px;
  padding: 10px;
  cursor: pointer;
}

.form__custom_input_file span {
  position: absolute;
  top: 50%;
  left: 10px;
  font-size: 0.75rem;
  transform: translateY(-50%);
  text-transform: uppercase;
  opacity: 0.6;
  color: #ACB5BD;
}

.form__custom_input_file::after {
  content: attr(data-text);
  position: absolute;
  top: 0;
  right: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 35%;
  height: 100%;
  background-color: #DDE2E5;
  border-top-right-radius: 8px;
  border-bottom-right-radius: 8px;
  transition: background-color 0.3s ease;
}

.form__custom_input_file:hover::after {
  background-color: #ACB5BD;
}

.form__textarea {
  width: 100%;
  height: 90px;
  padding: 10px;
  border: 1px solid #ACB5BD;
  border-radius: 10px;
  resize: none;
}

.form__textarea::placeholder {
  opacity: 0.6;
  color: #ACB5BD;
}

.form__checkbox {
  margin-bottom: 20px;
}

.form__btns {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.form__button {
  padding: 15px 73px;
}

@media (max-width: 375px) {
  .form__btns {
    flex-direction: column;
  }
  .form__button {
    width: 100%;
    margin-bottom: 15px;
  }
  .form__button:last-child {
    margin-bottom: 0;
  }
}

@media (max-width: 360px) {
  .form__label_heading {
    font-size: 0.875rem;
  }
  .form__button {
    padding: 12px 70px;
  }
  .form__custom_input_file {
    font-size: 0.875rem;
  }
}

@media (max-width: 320px) {
  .form__label {
    margin-bottom: 13px;
  }
}

.hero {
  padding: 200px 0 70px 0;
}

.hero__inner {
  display: flex;
  align-items: center;
  justify-content: center;
}

.hero__button {
  padding: 17px 80px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.hero__button span {
  display: inline-block;
  position: relative;
  font-size: 1.5rem;
  line-height: 27px;
  height: 27px;
  overflow: hidden;
  color: transparent;
}

.hero__button span::before, .hero__button span::after {
  content: attr(data-text);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  margin-right: -50%;
  color: #FFF;
  transition: transform 0.3s ease, opacity 0.3s ease;
}

.hero__button span::after {
  transform: translate(-50%, -25px);
  opacity: 0;
}

.hero__button:hover span::after {
  transform: translate(-50%, -50%);
  opacity: 1;
}

.hero__button:hover span::before {
  transform: translate(-50%, 25px);
  opacity: 0;
}

.hero__cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: -16px;
  margin-top: 40px;
}

.hero__cards_item {
  margin: 16px;
}

@media (max-width: 769px) {
  .hero {
    padding: 190px 0 40px 0;
  }
}

@media (max-width: 740px) {
  .hero__button {
    padding: 15px 60px;
    font-size: 1.3rem;
  }
}

@media (max-width: 480px) {
  .hero {
    padding: 151px 0 40px 0;
  }
}

@media (max-width: 320px) {
  .hero__button {
    padding: 10px 50px;
  }
}

.footer {
  background-color: #FFF;
  border-top: 2px solid #DDE2E5;
  box-shadow: 0.5px 0 7px rgba(221, 226, 229, 0.8);
}

.footer__inner {
  padding: 50px 0;
  display: flex;
}

.footer__link {
  position: relative;
  display: inline-block;
  font-size: 1.125rem;
  font-weight: bold;
  line-height: 1.2;
  text-decoration: none;
  color: #333;
  margin-left: auto;
  opacity: 0.5;
  transition: opacity 0.3s ease;
}

.footer__link::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: -3px;
  width: 100%;
  height: 1.5px;
  background-color: #333;
  transition: width 0.3s ease;
}

.footer__link:hover {
  opacity: 1;
}

.footer__link:hover::before {
  width: 0;
}

@media (max-width: 740px) {
  .footer__link {
    font-size: 1rem;
  }
}

@media (max-width: 480px) {
  .footer__inner {
    padding: 40px 0;
  }
}
